import React from "react";
import PropTypes from "prop-types";
import "./Movie.css";

function Movie({id, title, genres, summary, small_poster, large_poster, torrents}){
    return <div className="movie-wrapper">
        <img className = "movie-s-img" src = {small_poster} alt={title} title={title}></img>
        <img className = "movie-l-img" src = {large_poster} alt={title} title={title}></img>
        <div className="movie-data">
            <h3>{title}</h3>
            <ul className="movie-genres">
                {genres.map((genre, idx) => <li key={idx}>{genre}</li>)}
            </ul>

            <ul className="movie-torr">
                <li>Torr</li>
                {torrents.map((item, idx) => (
                    <li key = { idx }><a href={item.url}>{idx+1}</a></li>
                    ))}
            </ul>
            <div className = "movie-sum">
                <p>{summary}</p>
            </div>
        </div>
    </div>;
}

Movie.propTypes = {
    id : PropTypes.number.isRequired,
    title: PropTypes.string.isRequired,
    summary: PropTypes.string.isRequired,
    small_poster: PropTypes.string.isRequired,
    large_poster: PropTypes.string.isRequired,
    genres: PropTypes.arrayOf(PropTypes.string).isRequired
}

export default Movie;